import React, { Component } from 'react'
import SubNavbar from '../SubNavbar'
import Navbar from '../Navbar'
import Footer from '../Footer'
import axios from 'axios'
import { NavLink } from 'react-router'

export class AllProduct extends Component {
state={
    persons :[]
}
componentDidMount(){
    axios.get ('/assets/js/Api.json')
    .then((item)=>{
        this.setState(
            {
                persons :item.data.products
            }

        )



    })
}



  render() {
    return (
      <div>
        <SubNavbar/>
        <Navbar/>
        <div className='section properties'>
          <div className='container'>
            <div className='row properties-box'>
        {this.state.persons.map ((element)=>(
           <div className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
           <div className="item">
             <NavLink to="/Details"><img src={element.images[0]} alt="" style={{height:"350px"}}/></NavLink>
             <span className="category">{element.category}</span>
             <h6>${element.price}</h6>
             <h4><a href="property-details.html">{element.title}</a></h4>
             <ul>
               <li>rating: <span>{element.rating}</span></li>
               <li>stock: <span>{element.stock}</span></li>
               <li>discountPercentage: <span>{element.discountPercentage}</span></li>
               <li>brand: <span>{element.brand}</span></li>
               
             </ul>
             <div className="main-button">
               <NavLink to="/Details">Schedule a visit</NavLink>
             </div>
           </div>
         </div>

        ))}

            </div>
          </div>
        </div>
      <Footer/>
      </div>
      )
    }
  }
      

export default AllProduct