import React from 'react'
import SubNavbar from '../SubNavbar'
import Navbar from '../Navbar'
import Footer from '../Footer'
import PageHeading from './PageHeading'
import ContactPage from './ContactPage'

function AllContact() {
  return (
    <div>
        <SubNavbar/>
        <Navbar/>
        <PageHeading/>
        <ContactPage/>
        <Footer/>
    </div>
  )
}

export default AllContact