import React from 'react'
import SubNavbar from '../SubNavbar'
import Navbar from '../Navbar'
import Footer from '../Footer'
import PageHeading from './PageHeading'
import SectionProp from './SectionProp'

function AllProp() {
  return (
    <div>
        <SubNavbar/>
        <Navbar/>
        <PageHeading/>
        <SectionProp/>

        <Footer/>
    </div>
  )
}

export default AllProp